import React from "react";
import Button from "antd/es/button";

export default class Location extends React.Component {
  handleClick() {
    if (window.__WUJIE?.degrade || !window.Proxy || !window.CustomElementRegistry) {
      window.$wujie.location.href = "https://wujicode.cn/xy/app/prod/official/home";
    } else window.location.href = "https://wujicode.cn/xy/app/prod/official/home";
  }
  componentDidMount() {
    console.log("react16 location mounted")
  }

  render() {
    return (
      <div>
        <h2></h2>
        <div className="content">
          <h3>1</h3>
          <p></p>
          <p>urlquery keyname</p>
          <h3>2location</h3>
          <p>locationurlwujie</p>
          <h3>3window.location.host</h3>
          <p>{window.location.host}</p>
          <h3>4window.location.href</h3>
          <div className="p">
            <Button type="warning" onClick={this.handleClick}>
              
            </Button>
            <p>location.hrefshadowiframe</p>
            <p></p>
          </div>
        </div>
      </div>
    );
  }
}
